.newCaseForm {
    background-color: hsl(0, 0%, 95%);
    border-bottom: 1px solid #808080;
    padding: 2em;
    width: 55%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.newCaseForm input {
  background: hsl(0, 0%, 100%);
  border: none;
  border-bottom: 1px solid #808080;
  padding: .2em .4em;
  width: 60%;
  margin-bottom: .3em;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
}

.newCaseForm input:focus {
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
  outline: none;
  color: #f73939;
  border-bottom: 1px solid #f73939;
}

.newCaseForm button {
  background: #ffffff;
  border: 1px solid #afafaf;
  border-radius: 3px;
  padding: .35em .75em;
  -webkit-transition: .5s;
  -o-transition: .5s;
  transition: .5s;
  margin: .25em auto;
}

.newCaseForm>button:hover {
  border: 1px solid #f73939;
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
}







